import { CuratedSuggestedPrompt, getTaskIcon } from "@rp/react/common/hooks"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Skeleton, SxProps, Typography } from "@rp/react/web/mui/core"
import { Lock } from "@rp/react/web/mui/icons"

type Props = {
  onPromptClick?: (prompt: CuratedSuggestedPrompt) => void
  prompt: CuratedSuggestedPrompt
  isLoading?: boolean
  sx?: SxProps
}

const SuggestedPromptCard = ({ onPromptClick, prompt, isLoading }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Box
      onClick={() => (isLoading ? null : onPromptClick?.(prompt))}
      sx={theme => ({
        position: "relative",
        height: isMobile ? undefined : "176px",
        backgroundColor: theme.palette.elevation.dp01,
        padding: "12px",
        cursor: "pointer",
        borderRadius: "8px",
        boxShadow: "0px 4px 24px 0px rgba(0, 0, 0, 0.65)",
        "&:hover": {
          backgroundColor: theme.palette.elevation.dp03,
        },
      })}
      className={"qaa-suggested-prompt-card"}
    >
      {prompt.blocked ? (
        <Box
          sx={{
            position: "absolute",
            top: 6,
            right: 6,
            px: 0.5,
            py: 0.5,
            bgcolor: "#C9AC1233",
            borderRadius: 1,
            display: "flex",
            "& > svg": {
              color: "#C9AC12",
              fontSize: 12,
            },
          }}
        >
          <Lock />
        </Box>
      ) : null}
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          color: prompt.icon.color,
          size: 14,
          gap: 0.5,

          "& svg": {
            width: 20,
            height: 20,
          },
        }}
      >
        {isLoading ? <Skeleton width={20} /> : getTaskIcon(prompt.icon.id)}
        {prompt.icon.label}
      </Box>
      <Box sx={{ mt: 1.5 }}>
        {isLoading ? (
          <>
            <Skeleton />
            <Skeleton />
            <Skeleton />
            <Skeleton width={"50%"} />
          </>
        ) : (
          <Typography
            sx={{
              fontSize: 18,
              height: "100%",
              overflow: "hidden",
              display: "-webkit-box",
              overflowWrap: "break-word",
              WebkitBoxOrient: "vertical",
              WebkitLineClamp: isMobile ? 3 : 5,
            }}
          >
            {prompt.label}
          </Typography>
        )}
      </Box>
    </Box>
  )
}
export { SuggestedPromptCard }
